label {
  display: block;
}
.App {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-content: space-between;
}

.Header {
  color: aqua;
  text-align: center;
  text-decoration: aqua underline;
}

.Search {
  align-self: center;
  margin: 50px auto;
}

li {
  list-style: none;
  margin: 10px auto;
}

label {
  width: 100px;
  margin: 10px;
  color: blueviolet;
  display: inline-block;
}

input {
  display: inline-block;
  margin: 10px;
  padding: 3px;
  outline: cornflowerblue outset 2px;
  width: 200px;

}

textarea {
  display: inline-block;
  margin: 10px;
  padding: 3px;
  outline: cornflowerblue inset 2px;
  width: 200px;
height: 300px;
}

button {
  margin: 10px 45%;
  background-color: azure;
  width: 80px;
  font-size: 21px;
  cursor: pointer;
  border-radius: 10px;
  border: darkslategrey 1px solid;
}

button:hover {
  background-color: deeppink;
  color: darkturquoise;
  border: whitesmoke 1px solid;
}